.featured {
  height: 100vh;

}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%,transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%,transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
  
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.features--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}


.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-right: 15px; 
}

.featured--points{
  color: #46d369;
}
.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons,.featured--genres {
  margin-top: 15px;
}


.featured--watchbutton,.featured--mylistbutton {
  display: inline-block;


}
@media (max-width: 760px) {
  .featured {
    height: 90vh;
  }
  .featured--name {
    font-size: 40px;
  }
  .featured--info {
    font-size: 16px;
  }
  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;

  }
  .featured--watchbutton,.featured--mylistbutton {
    font-size: 16px;
  }
  .featured--genres {
    font-size: 14px;
  }
}